#slider-wrap {
  width: 100%;
  height: 340px;
  position: relative;
  overflow: hidden;
}
#slider-wrap ul#slider {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
#slider-wrap ul#slider {
  width: 100%;
}
#slider-wrap ul#slider li {
  float: left;
  position: relative;
  overflow: hidden;
  height: 100%;
}
.btns {
  position: absolute;
  width: 36px;
  height: 60px;
  top: 50%;
  margin-top: -30px;
  line-height: 57px;
  text-align: center;
  cursor: pointer;
  background: rgba(88, 77, 77, 0.62);
  z-index: 100;
}
#counter {
  top: 30px;
  right: 35px;
  width: auto;
  position: absolute;
}
#slider-wrap #next {
  right: 0;
}
#slider-wrap #previous {
  left: 0;
}
#pagination-wrap {
  position: absolute;
  height: 15px;
  bottom: 20px;
  left: 46%;
}
#pagination-wrap ul {
  width: 100%;
}
#pagination-wrap ul li {
  margin: 0 4px;
  display: inline-block;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: #272727;
  opacity: 0.5;
  position: relative;
  top: 0;
}
#pagination-wrap ul li.active {
  width: 12px;
  height: 12px;
  top: 3px;
  opacity: 1;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 1px 0;
}

#slider-wrap ul,
#pagination-wrap ul li {
  -webkit-transition: all 0.3s cubic-bezier(1, 0.01, 0.32, 1);
  -moz-transition: all 0.3s cubic-bezier(1, 0.01, 0.32, 1);
  -o-transition: all 0.3s cubic-bezier(1, 0.01, 0.32, 1);
  -ms-transition: all 0.3s cubic-bezier(1, 0.01, 0.32, 1);
  transition: all 0.3s cubic-bezier(1, 0.01, 0.32, 1);
}

.qrcode-tip {
  padding: 12px;
  max-width: 200px;
}
.head-img{
  max-width: 200px;
}

