.bg {
  animation: slide 3s ease-in-out infinite alternate;
  background-image: linear-gradient(-50deg, rgb(255, 219, 110) 50%, rgb(255, 184, 52) 20%);
  bottom: 0;
  left: -50%;
  opacity: 0.6;
  position: fixed;
  right: -50%;
  top: 0;
  z-index: -1;
}

.bg2 {
  animation-direction: alternate-reverse;
  animation-duration: 4s;
}

.bg3 {
  animation-duration: 5s;
}

@keyframes slide {
  0% {
    transform: translateX(-25%);
  }
  100% {
    transform: translateX(25%);
  }
}
