@media (max-width: 52em) {
  .flex-tabs {
    flex-direction: column;
  }
  .flex-tabs .tab {
    border-bottom: 1px solid #fff;
  }
  .flex-tabs .tab:last-of-type {
    border-bottom: none;
  }
  .flex-tabs #tab-1-label,.flex-tabs #tab-2-1-label,.flex-tabs #tab-3-1-label {
    order: 1;
  }
  .flex-tabs #tab-2-label,.flex-tabs #tab-2-2-label,.flex-tabs #tab-3-2-label {
    order: 3;
  }
  .flex-tabs #tab-3-label,.flex-tabs #tab-2-3-label,.flex-tabs #tab-3-3-label {
    order: 5;
  }
  .flex-tabs #tab-4-label,.flex-tabs #tab-2-4-label,.flex-tabs #tab-3-4-label {
    order: 7;
  }
  .flex-tabs #tab-5-label,.flex-tabs #tab-2-5-label,.flex-tabs #tab-3-5-label {
    order: 9;
  }
  .flex-tabs #tab-1-panel,.flex-tabs #tab-2-1-panel,.flex-tabs #tab-3-1-panel {
    order: 2;
  }
  .flex-tabs #tab-2-panel,.flex-tabs #tab-2-2-panel,.flex-tabs #tab-3-2-panel {
    order: 4;
  }
  .flex-tabs #tab-3-panel,.flex-tabs #tab-2-3-panel,.flex-tabs #tab-3-3-panel {
    order: 6;
  }
  .flex-tabs #tab-4-panel,.flex-tabs #tab-2-4-panel,.flex-tabs #tab-3-4-panel {
    order: 8;
  }
  .flex-tabs #tab-5-panel,.flex-tabs #tab-2-5-panel,.flex-tabs #tab-3-5-panel {
    order: 10;
  }

  #tab-1:checked ~ .tabs #tab-1-panel,
  #tab-2:checked ~ .tabs #tab-2-panel,
  #tab-3:checked ~ .tabs #tab-3-panel,
  #tab-4:checked ~ .tabs #tab-4-panel,
  #tab-5:checked ~ .tabs #tab-5-panel,
  #tab-2-1:checked ~ .tabs #tab-2-1-panel,
